.card {
	display: flex;
	flex-direction: column;
	position: relative;
	overflow: hidden;
	height: 100%;

	/* padding: 0px 0px 58px 0px;
	margin: 0px 0px 20px 0px; */
}

.image {
	display: block;
	max-width: 100%;
	height: 100%;
	object-fit: cover;
	border-radius: 15px;

	filter: brightness(40%);

	/* width: 100%;
	position: absolute;
	top: 0;
	left: 0; */
}

.image:hover {
	filter: none;
	/* -webkit-filter: contrast(10%); */
}

.content {
	text-align: center;

	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	z-index: 1;
}

.title {
	/* border: 2px solid #d7aaaa;
	background-color: #d7aaaa;
	border-radius: 1rem; */
	color: white;
	font-family: fangsong;
	font-weight: 400;
	font-size: 38px;
	letter-spacing: 0.2vw;
}

@media (max-width: 1200px) {
	.title {
		font-size: calc(18px + (38 - 18) * ((100vw - 320px) / (1432 - 320)));
	}
}
